import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import { InlineNotification } from "carbon-components-react";

const dispatchedEvents = ["ABOUT#clicked"];

/*
    Example: <srs-highlightvalue value="This is the complete string" highlight="complete"/>
*/
const SrsHighlightedValue = function (props, ref) {
    const { value, highlight } = props;

    // Find the index of the first occurrence of the highlight string
    const highlightIndex = value.indexOf(highlight);

    // If the highlight string is not found, render the value as is
    if (highlightIndex === -1) {
        return <span>{value}</span>;
    }

    // Split the value into three parts: the text before the highlight,
    // the highlighted text, and the text after the highlight
    const beforeHighlight = value.slice(0, highlightIndex);
    const highlightedText = value.slice(highlightIndex, highlightIndex + highlight.length);
    const afterHighlight = value.slice(highlightIndex + highlight.length);

    return (
        <span>
        {beforeHighlight}
        <strong>{highlightedText}</strong>
        {afterHighlight}
        </span>
    );
}

const styles = `
    
`
const props = [{
    name: "value",
    defaultValue: "",
    type: "string"
}, {
    name: "highlight",
    defaultValue: "",
    type: "string"
}];

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsHighlightedValue), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };